import {mapState} from 'vuex';

import list from '@/components/chat/list/index.vue'
import chatInput from '@/components/chat/input/index.vue'
import join from '@/components/chat/join/index.vue'
import url from '@/components/events/event/url/index.vue'

import _ from 'underscore'


export default {
  name: 'chat',
  props: {
    chat: Object,
    u: String,
  },

  components: {
    list,
    chatInput,
    join,
    url
  },

  data: function () {
    return {
      roomMuted: false,
      loading: false,
      usersinfo: [],
      prevImage: undefined,
      selectedImg: String,
      urlpreview: null,

      filePreview: {
        type: Object,
        default: function () {
          return {
            show: false
          }
        },
      },
      anyFilePreview: {
        type: Object,
        default: function () {
          return {
            show: false
          }
        }
      },


    }

  },

  created() {

  },

  mounted() {

    this.getuserinfo()
    this.$store.commit('active', true)
    this.$store.commit('blockactive', {value: true, item: 'chat'})

  },

  destroyed() {
    this.$store.commit('blockactive', {value: false, item: 'chat'})
    this.$store.commit('SET_CURRENT_ROOM', false)
  },

  watch: {
    chat: {
      immediate: true,
      handler: function () {
        if (this.chat)
          this.$store.commit('SET_CURRENT_ROOM', this.chat.roomId)
        else
          this.$store.commit('SET_CURRENT_ROOM', false)
      }
    }
  },

  computed: mapState({

    pocketnet: state => state.pocketnet,
    minimized: state => state.minimized,
    active: state => state.active,

    auth: state => state.auth,
    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        let pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
        if (pushRules !== null) {
          this.roomMuted = true
        }
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        return m_chat || {}
      }

    },

    encrypted: function () {
      if (this.chat && this.chat.roomId) {
        return this.core.mtrx.client.isRoomEncrypted(this.chat.roomId)
      }

      return false

    },
    openInviteModal: function () {
      return this.openInviteModal
    }


  }),
  methods: {
    scrollBottom(){
     return true
    },
    hidePreview: function () {
      this.urlpreview = null
    },
    newchat: function (chat) {
      this.$emit("newchat", chat)
    },

    sending: function () {
      this.$emit('sending')

      $(this.$el).find('.eventsflex').scrollTop(0)
    },

    getuserinfo: function () {

      if (this.u) {
        this.core.user.usersInfo(this.u).then(info => {
          this.usersinfo = info
        })
      }
    },

    membership: function () {

      if (this.m_chat) {
        return this.m_chat.getMyMembership()
      }

    },
    closing: function (e) {
      this.PNmetaPreview = e
    },
    usersinfoNames: function () {
      return _.map(this.usersinfo, function (u) {
        return u.name
      }).join(', ')
    },

    openImgM(img) {
      this.selectedImg = img
    },

    getFilePreview(data) {
      this.filePreview = data
    },

    anyFileData(data) {
      this.anyFilePreview.name = data.file.name
      this.anyFilePreview.size = data.file.size
    },

    chatUrlHandler(url) {

      this.urlpreview = url

    },
    events(data) {
      this.$emit('getEvents', data)
    }

  }
}